.post-load {
  transition: -webkit-transform 2s ease-in-out;
  transition: transform 2s ease-in-out;
  transition: transform 2s ease-in-out, -webkit-transform 2s ease-in-out;
  -webkit-transform: translate(0, 0) !important;
          transform: translate(0, 0) !important;
}

#preloader {
  position: relative;
  width: 100%;
  height: 100%;
}
#preloader > h4 {
  top: 43%;
  margin: 0;
  margin-bottom: 50px;
  position: relative;
  display: block;
  opacity: 0;
  text-align: center;
  color: gray;
  transition: color 0.5s ease-in-out;
}
#preloader > #pre-text-2 {
  display: none;
  opacity: 0;
}
#preloader > .progress {
  top: 51%;
  left: 30%;
  width: 40%;
  height: 5px;
  position: absolute;
}
.pace {
  -webkit-pointer-events: none;
  pointer-events: none;

  -webkit-user-select: none;
  user-select: none;

  z-index: 2000;
  margin: auto;
  height: 5px;
  width: 100%;
  position: relative;
  background: transparent;
  overflow: hidden;
}
.pace .pace-progress {
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box;

  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  position: absolute;
  width: 100%;
  z-index: 2000;
  position: relative;
  right: 100%;
  height: 100%;
  width: 100%;
}
.pace.pace-inactive {
  display: none;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html,
body {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  font-family: Montserrat, Arial, sans-serif;
}
.App,
#root {
  height: 100vh;
  width: 100%;
  display: block;
  position: relative;
}
/*Header Tags*/
h1 {
  font-size: 12em;
}
h2 {
  font-size: 10em;
}
h3 {
  font-size: 8em;
  font-weight: 600;
}
h4 {
  font-size: 2.25em;
  font-weight: 700;
}
h5 {
  font-size: 1.8em;
  font-weight: 700;
}
/*Generic Container Styles*/
section {
  height: 100%;
  width: 100%;
  transition: opacity 0.2s ease-in-out;
}

@media all and (max-width: 615px) {
